"use client";

import type { AgentInfo } from "@/lib/types";
import { Tag } from "@/components/shared/Tag";
import { Button } from "@/components/shared/Button";
import { EmptyState } from "@/components/shared/EmptyState";

interface RunningAgentsProps {
  agents: AgentInfo[];
  onTerminate: (agentId: string) => void;
}

function stateVariant(state: string) {
  if (state === "running") return "success" as const;
  if (state === "error") return "danger" as const;
  if (state === "ready") return "info" as const;
  return "default" as const;
}

function formatUptime(seconds: number): string {
  if (seconds < 60) return `${seconds}s`;
  if (seconds < 3600) return `${Math.floor(seconds / 60)}m ${seconds % 60}s`;
  const h = Math.floor(seconds / 3600);
  const m = Math.floor((seconds % 3600) / 60);
  return `${h}h ${m}m`;
}

export function RunningAgents({ agents, onTerminate }: RunningAgentsProps) {
  if (!agents.length) {
    return <EmptyState message="No agents running" />;
  }

  return (
    <div className="space-y-3">
      {agents.map((agent) => (
        <div
          key={agent.id}
          className="glass-card px-4 py-3 flex items-center justify-between"
        >
          <div className="flex-1 min-w-0">
            <div className="flex items-center gap-2 mb-1">
              <span className="font-medium text-sm text-white truncate">
                {agent.name || agent.id}
              </span>
              <Tag variant={stateVariant(agent.state)}>{agent.state}</Tag>
              {agent.model && (
                <span className="text-2xs text-purple-300/70 bg-purple-500/10 px-1.5 py-0.5 rounded">
                  {agent.model}
                </span>
              )}
            </div>
            <div className="flex items-center gap-3 text-2xs text-white/30">
              <span title={agent.id}>PID: {agent.id.slice(0, 8)}</span>
              <span>Trust: {agent.trustLevel || "—"}</span>
              <span>Uptime: {formatUptime(agent.uptime)}</span>
              {agent.tokenUsage && (agent.tokenUsage.input > 0 || agent.tokenUsage.output > 0) && (
                <span>
                  Tokens: {agent.tokenUsage.input}in / {agent.tokenUsage.output}out
                </span>
              )}
              {(agent.errorCount ?? 0) > 0 && (
                <span className="text-red-400">Errors: {agent.errorCount}</span>
              )}
            </div>
          </div>
          <Button
            variant="danger"
            onClick={() => onTerminate(agent.id)}
            className="shrink-0 ml-3"
          >
            Terminate
          </Button>
        </div>
      ))}
    </div>
  );
}
